import ChequeTypeService from '@/services/cheque/ChequeTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let ChequeTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultChequeTypeTableFilter: {
      type: String,
      default: null
    },
    defaultChequeTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      chequeTypeTableData: [],
      chequeTypeTableFilterTimer: null,
      chequeTypeTableFilter: this.defaultChequeTypeTableFilter,
      chequeTypeTableOrderBy: this.defaultChequeTypeTableOrderBy,
      chequeTypeTableContainer: 'loading-container',
      chequeTypeTableBusy: false,
      chequeTypeTableTotalRows: 0,
      chequeTypeTableCurrentPage: 1,
      chequeTypeTablePerPage: 10
    }
  },
  computed: {
    chequeTypeTableNoDataText: function () {
      return this.chequeTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    chequeTypeTableTotalPages: function () {
      return Math.ceil(this.chequeTypeTableTotalRows / this.chequeTypeTablePerPage)
    },
    chequeTypeTablePagingInfo: function () {
      return this.pagingInfo(this.chequeTypeTableCurrentPage, this.chequeTypeTablePerPage, this.chequeTypeTableTotalRows)
    }
  },
  methods: {
    loadChequeTypeTableData (loadingContainerId) {
      let self = this
      this.chequeTypeTableBusy = true
      this.chequeTypeTableContainer = loadingContainerId || this.chequeTypeTableContainer
      this.startLoading(this.chequeTypeTableContainer)
      ChequeTypeService.chequeTypes({
        pageIndex: this.chequeTypeTableCurrentPage - 1,
        pageSize: this.chequeTypeTablePerPage,
        filter: this.chequeTypeTableFilter,
        orderByField: this.chequeTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.chequeTypeTableContainer)
        self.chequeTypeTableBusy = false
        self.chequeTypeTableTotalRows = response.data.totalRows
        self.chequeTypeTableData = response.data.chequeTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.chequeTypeTableContainer)
        self.chequeTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshChequeTypeTable () {
      this.loadChequeTypeTableData()
    },
    filterChequeTypeTable (filter) {
      let self = this
      if (this.chequeTypeTableFilter !== filter) {
        clearTimeout(this.chequeTypeTableFilterTimer)
        this.chequeTypeTableFilterTimer = setTimeout(() => {
          self.chequeTypeTableFilter = filter
          self.loadChequeTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.chequeTypeTableFilterTimer)
      }
    },
    orderChequeTypeTable (orderBy, active) {
      this.chequeTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadChequeTypeTableData()
    }
  },
  watch: {
    chequeTypeTableCurrentPage: function () {
      this.refreshChequeTypeTable()
    },
    chequeTypeTablePerPage: function () {
      this.refreshChequeTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshChequeTypeTable()
    }
  }
}

export default ChequeTypeTableMixin
